@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;500&family=Signika:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
@font-face {
    font-family: 'myFont';
    src: url('./myfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}


.footer-big-container{
    margin-top: 40px;
    font-family: 'Poppins', sans-serif;
    font-weight: 300 !important;
    width: 100%;
    background-color: #121212;
    color:white;
    min-height: 300px;
    box-sizing: border-box;
}
#footer-logo{
    color: white;
    font-size: 100px;
    font-family: 'myFont',sans-serif;
    font-weight: 500;
    
}
.items-links-container{
    list-style: none;
    width: 180px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}


.icons-brands{
    color:white;
    font-size: 35px;
    padding: 0px 10px;
}


.footer-container{
    display: flex;
    flex-direction: row;
    padding-top: 20px;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    gap: 100px;
}
.footerLeft{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 25px;
}
.links-media-link{
    -webkit-tap-highlight-color: transparent;
}

@media (max-width:500px) {
    #footer-logo{
        font-size: 60px;
    }
    .footer-container{
        padding-top: 0px;
    }
    .icons-brands{
        font-size: 25px;
    }
    .footerLeft{
        gap: 25px;
    }
    .footer-big-container{
        min-height: 200px;
    }
}