.product-container-card{
    width: 275px;
    height: 400px;
    box-shadow: 1px 1px 10px grey;
    border: 1px solid white;
    box-sizing: border-box;
    border-radius: 10px;
}
.card-container-img-text{
    display: flex;
    border: 2px ;
    flex-direction: column;
    align-items: center;
}

.card-img{
    width: 100%;
    height: 256px;
    overflow-y: hidden;
}
.real-img{
    width: 100%;
    height: 300px;
    padding-bottom: 16px;
}
.card-text{
    height: 100%;
}
.card-product-info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 17px;
}
.product-title{
    font-size: 22px;
    font-weight: bold;
    padding-top: 0;
}

.product-price-info .wrong-price{
    color: red;
    padding-right: 7px;
}
.product-price-info .correct-price{
    padding-right: 7px;
}
.card-button{
    background-color: #121212;
    color: white;
    text-decoration: none;
    height: 36px;
    font-size: 18px;
}

@media (max-width:900px) {
    .product-container-card{
        width: 150px;
        height: 220px;
    }

    .card-img{
        height: 105px;
        width: 100%;
    }
    .product-title
    {
        font-size: 13px;
    }
    .product-price-info{
        font-size: 12px;
    }
    .card-button{
        border-radius: 10px;
    }
    .card-product-info{
        margin-top: 25px;
        gap: 5px !important;
    }
    .real-img{
        height: 135px;
        padding-bottom: 0;
        width: 145px;
    }
}