@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
.all-card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;
    align-items: center;
}
.heading-of-card{
    font-family: 'Poppins', sans-serif;
    text-align: center;
    padding-top: 40px;
    margin-bottom: 40px;
}

@media (max-width:900px) {
    .all-card-container{
        gap: 20px;
    }
}